body {
  margin: 0;
  min-height: 100vh;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.bg-header {
  background: url('https://res.cloudinary.com/bcimbali/image/upload/c_scale,dpr_auto,f_auto,q_auto/v1535911207/twin-peaks-memory-game/dark_wood.png');
}

.card-holder img {
  height: 200px;
  width: 200px;
}

.colors {
  background-color: #634643;
  color: rgb(219, 26, 12);
  text-shadow: 5px 5px 5px #000;
}

.colors a {
  color: rgb(219, 26, 12);
}

.tp-text {
  font-family: 'Trade Winds', cursive;
  color: rgb(219, 26, 12);
  text-shadow: -1px -1px 0 rgb(219, 26, 12),
    1px -1px 0 rgb(219, 26, 12),
    -1px 1px 0 rgb(219, 26, 12),
    1px 1px 0 rgb(219, 26, 12),
    4px 3px 0.5px #000;
}

.tp-text a {
  color: rgb(219, 26, 12);
  text-shadow: -1px -1px 0 rgb(219, 26, 12),
    1px -1px 0 rgb(219, 26, 12),
    -1px 1px 0 rgb(219, 26, 12),
    1px 1px 0 rgb(219, 26, 12),
    4px 3px 0.5px #000;
}

.tp-text a:hover {
  color: rgb(219, 26, 12);
  text-shadow: -1px -1px 0 #634643,
    1px -1px 0 #634643,
    -1px 1px 0 #634643,
    1px 1px 0 #634643,
    4px 3px 0.5px #000;
}

.neon {
  font-family: 'Lato', sans-serif;
  text-transform: uppercase;
  color: #fff;
  text-shadow: 0 0 5px #fff,
    0 0 10px rgb(255, 181, 181),
    0 0 20px #e40b0b,
    0 0 30px #e40b0b,
    0 0 40px #e40b0b,
    0 0 55px #e40b0b,
    0 0 75px #e40b0b;
  text-align: center;
}

.bg-nav {
  background: rgba(94, 86, 85, 0.8);
}

.footer-100 {
  bottom: 0;
  height: 8vh;
  left: 0;
  width: 100%;
}

.footer-mgn {
  top: 5vh;
}

.grow {
  transition: all .1s ease-in-out;
}

.grow:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.header-font {
  font-size: 4vw;
}

.head-mar-top {
  margin-top: 0vh;
}

.header-pad {
  padding: 15vh 0 10vh 0;
}

.icon-font {
  font-size: calc(10px + 14 * (100vw - 320px)/ 800);
}

.lodge {
  background: rgb(247, 44, 44);
  background: linear-gradient(to right, #000000, rgb(247, 44, 44));
}

.main-content {
  max-width: 70vw;
}

.red {
  color: red;
}

.nav-font {
  font-size: 2vw;
}

.nav-font a {
  font-size: 2vw;
}

.nav-calc-font {
  font-size: calc(10px + 14 * (100vw - 320px)/ 800);
}

.nav-shadow {
  box-shadow: 0 14px 28px rgba(0, 0, 0, .25), 0 10px 10px rgba(0, 0, 0, .22);
}

.nav-width {
  min-width: 100vw;
}

.nav-height {
  min-height: 10vh;
}

.nav-li-width {
  min-width: 33vw;
}

.padding-main {
  padding-bottom: 5vh;
}

.pointer {
  cursor: pointer;
}

.title-line-hgt {
  line-height: 2rem;
}

.card-select:hover {
  box-shadow: 0 3px 6px rgba(0, 0, 0, .16), 0 3px 6px rgba(0, 0, 0, .23);
}

.h-100vh {
  min-height: 100vh;
}

/* ANIMATIONS */

@-webkit-keyframes jello-vertical {
  0% {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
  30% {
    -webkit-transform: scale3d(0.75, 1.25, 1);
    transform: scale3d(0.75, 1.25, 1);
  }
  40% {
    -webkit-transform: scale3d(1.25, 0.75, 1);
    transform: scale3d(1.25, 0.75, 1);
  }
  50% {
    -webkit-transform: scale3d(0.85, 1.15, 1);
    transform: scale3d(0.85, 1.15, 1);
  }
  65% {
    -webkit-transform: scale3d(1.05, 0.95, 1);
    transform: scale3d(1.05, 0.95, 1);
  }
  75% {
    -webkit-transform: scale3d(0.95, 1.05, 1);
    transform: scale3d(0.95, 1.05, 1);
  }
  100% {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}

@keyframes jello-vertical {
  0% {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
  30% {
    -webkit-transform: scale3d(0.75, 1.25, 1);
    transform: scale3d(0.75, 1.25, 1);
  }
  40% {
    -webkit-transform: scale3d(1.25, 0.75, 1);
    transform: scale3d(1.25, 0.75, 1);
  }
  50% {
    -webkit-transform: scale3d(0.85, 1.15, 1);
    transform: scale3d(0.85, 1.15, 1);
  }
  65% {
    -webkit-transform: scale3d(1.05, 0.95, 1);
    transform: scale3d(1.05, 0.95, 1);
  }
  75% {
    -webkit-transform: scale3d(0.95, 1.05, 1);
    transform: scale3d(0.95, 1.05, 1);
  }
  100% {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}

.jello-vertical {
  -webkit-animation: jello-vertical 1.3s both;
  animation: jello-vertical 1.3s both;
}

/* Flicker */

@-webkit-keyframes flicker-1 {
  0%, 100% {
    opacity: 1;
  }
  41.99% {
    opacity: 1;
  }
  42% {
    opacity: 0;
  }
  43% {
    opacity: 0;
  }
  43.01% {
    opacity: 1;
  }
  47.99% {
    opacity: 1;
  }
  48% {
    opacity: 0;
  }
  49% {
    opacity: 0;
  }
  49.01% {
    opacity: 1;
  }
}

@keyframes flicker-1 {
  0%, 100% {
    opacity: 1;
  }
  41.99% {
    opacity: 1;
  }
  42% {
    opacity: 0;
  }
  43% {
    opacity: 0;
  }
  43.01% {
    opacity: 1;
  }
  47.99% {
    opacity: 1;
  }
  48% {
    opacity: 0;
  }
  49% {
    opacity: 0;
  }
  49.01% {
    opacity: 1;
  }
}

.flicker-1 {
  -webkit-animation: flicker-1 4s linear infinite both;
  animation: flicker-1 4s linear infinite both;
}

/* Shake */

@-webkit-keyframes shake-horizontal {
  0%, 100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  10%, 30%, 50%, 70% {
    -webkit-transform: translateX(-10px);
    transform: translateX(-10px);
  }
  20%, 40%, 60% {
    -webkit-transform: translateX(10px);
    transform: translateX(10px);
  }
  80% {
    -webkit-transform: translateX(8px);
    transform: translateX(8px);
  }
  90% {
    -webkit-transform: translateX(-8px);
    transform: translateX(-8px);
  }
}

@keyframes shake-horizontal {
  0%, 100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  10%, 30%, 50%, 70% {
    -webkit-transform: translateX(-10px);
    transform: translateX(-10px);
  }
  20%, 40%, 60% {
    -webkit-transform: translateX(10px);
    transform: translateX(10px);
  }
  80% {
    -webkit-transform: translateX(8px);
    transform: translateX(8px);
  }
  90% {
    -webkit-transform: translateX(-8px);
    transform: translateX(-8px);
  }
}

.shake-horizontal {
  -webkit-animation: shake-horizontal 0.8s cubic-bezier(0.455, 0.030, 0.515, 0.955) both;
  animation: shake-horizontal 0.8s cubic-bezier(0.455, 0.030, 0.515, 0.955) both;
}

/* MEDIA QUERIES */

@media screen and (max-width: 768px) {
  .nav-font {
    font-size: 0.75rem;
    /* font-size: calc(25px + (100 - 14) * ((40vw - 300px) / (1600 - 300))); */
  }
  .nav-font a {
    font-size: 0.75rem;
  }
  .title-line-hgt {
    line-height: 1.5rem;
  }
  .header-font {
    font-size: 6vw;
  }
  .header-pad {
    padding: 15vh 0 5vh 0;
  }
  .tp-text {
    color: rgb(219, 26, 12);
    text-shadow: none;
  }
  .tp-text a {
    color: rgb(219, 26, 12);
    text-shadow: none;
  }
}

@media screen and (max-width: 450px) {
  .nav-font {
    font-size: 0.5rem;
    /* font-size: calc(25px + (100 - 14) * ((40vw - 300px) / (1600 - 300))); */
  }
  .nav-font a {
    font-size: 0.5rem;
  }
  .navbar li {
    width: 40%;
  }
  .title-line-hgt {
    line-height: 1rem;
  }
}
